:export{red:#ff444f;light_chart_text:#555975;dark_chart_text:#999;light_chart_grid:#f2f3f4;dark_chart_grid:#1d1f20;light_chart_mountain_border:#85acb0;light_chart_mountain_bg:rgba(133,172,176,0.16);light_chart_mountain_bg_shade:rgba(255,255,255,0);light_chart_closed_mountain_border:rgba(133,172,176,0.32);dark_chart_mountain_border:#fff;dark_chart_mountain_bg:rgba(255,255,255,0.36);dark_chart_mountain_bg_shade:rgba(16,19,32,0);dark_chart_closed_mountain_border:rgba(255,255,255,0.32);chart_closed_candle_up:rgba(133,172,176,0.32);chart_closed_candle_down:rgba(255,68,79,0.32);chart_closed_candle_border:rgba(51,51,51,0.32);chart_current_hr:#ff444f;chart_closed_current_hr:rgba(255,68,79,0.32);light_chart_bg:#fff;dark_chart_bg:#0e0e0e;light_float_labels_bg:#d6dadb;dark_float_labels_bg:#323738;light_float_labels_text:#333;dark_float_labels_text:#fff}@keyframes cq-slide-down{0%{top:-32px;opacity:0}100%{opacity:1;top:0}}@keyframes cq-slide-up{100%{height:0;opacity:0}}.cq-notifications{position:absolute;right:10px;top:20px;z-index:+3;background:transparent;display:flex;flex-direction:column;align-items:flex-end}.cq-notifications .notification{position:relative;margin-top:4px;height:32px;animation:cq-slide-down 0.3s;display:flex;justify-content:flex-start;align-items:center;font-size:1.4em;border:1px solid #fdd063;border-radius:3px}.cq-notifications .notification.warning{background:#fff8e2}.cq-notifications .notification.success{border-color:#008006;background:#cceea1}.cq-notifications .notification.info{border-color:#009afa;background:#e0f2fe}.cq-notifications .notification.error{border-color:#ff3d38;background:#ffeaee}.cq-notifications .notification.hide{animation:cq-slide-up 0.3s forwards}.cq-notifications .notification.down{margin-top:50px}.cq-notifications .notification .close-icon{background:svg-load("./icons/ic-close.svg") no-repeat scroll center center;cursor:pointer;margin:0px 5px;position:relative;padding:5px}.cq-notifications .notification .ic-icon{position:relative;padding:0 5px}.cq-notifications .notification .icon-notification-warning{background:svg-load("./icons/warning.svg") no-repeat scroll center center}.cq-notifications .notification .icon-notification-error{background:svg-load("./icons/error.svg") no-repeat scroll center center}.cq-notifications .notification .icon-notification-info{background:svg-load("./icons/info.svg") no-repeat scroll center center}.cq-notifications .notification .icon-notification-success{background:svg-load("./icons/success.svg") no-repeat scroll center center}.smartcharts-mobile .cq-notifications{position:relative;right:0px;top:0px}
